.wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.control-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.controls {
  display: table;
}

.control {
  display: table-row;
  padding: 5px 0;
}

.control-name, .control-input {
  display: table-cell;
  vertical-align: middle;
}

.control-name {
  text-align: center;
  padding-bottom: 12px;

  font-size: 1.3rem;
  text-transform: uppercase;
}

.control-input-wrap {
  display: -ms-flexbox;
